}
.App {
 
  text-align: center;

}
.scroll {
  position: absolute;
  width: 100vw;
  height: 400vh;
  overflow-y: auto;
  top: 0;
  left: 0;
}
.Loader .gg {
  position:absolute;
  display:block;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%)
 }
